import LevelBase from "./levelBase";
import { Exit, Interactive, Player, Torch } from "../objects";
import Phaser from "phaser";
import UI from "../UI";
import { translate } from "../../utils/localization";
import { TICK_TIME } from "../../utils/constants";
import template from "lodash.template";

class Level17 extends LevelBase {
  constructor() {
    super();
    this.name = "Into the dark";
    this.diamondsGoal = 10;
    this.quakePlayed = false;
  }

  get id() {
    return this.constructor.key;
  }

  connectToScene(scene) {
    super.connectToScene(scene);
    this.destroyBlock = () => null;
    //  Enable lights and set a dark ambient color
    scene.lights.enable().setAmbientColor(0x000000);

    if(this.unfinished) {
      // adds light on angel
      this.staticlight = scene.lights
        .addLight(
          UI.tileToScreen(this.exit.position.x),
          UI.tileToScreen(this.exit.position.y),
          1000
        )
        .setColor(0xeaeaea)
        .setIntensity(1.5);
      scene.time.addEvent({
        delay: 100,
        callback: function () {
          if (!this.exit.block.alien.active && this.staticlight.intensity > 0) {
            this.staticlight.setIntensity(this.staticlight.intensity - 0.5);
          }
          Phaser.Geom.Ellipse.Random(this.exit.block.alien, this.staticlight);
        },
        callbackScope: this,
        repeat: -1,
      });
    } else {
      // ============
      // removes NPCs
      // ============
      this.replaceByEmpty(this.leader);
      this.leader.alien.destroy();
      this.leader = null;
      // ================================
      // removes usless interactive spots
      // ================================
      Object.keys(this.interactiveElements).forEach((k) => {
        // removes if is not the exit
        if (!this.interactiveElements[k].isClass(Exit)) {
          this.interactiveElements[k] = null;
        }
      });
    }

    scene.children.getByName("layer_0").setPipeline("Light2D");
    scene.children.getByName("layer_1").setPipeline("Light2D");
    scene.children.getByName("layer_2").setPipeline("Light2D");
    scene.children.getByName("layer_1z2").setPipeline("Light2D");
    this.moveables.forEach(
      (m) => !m.isClass(Player) && m.alien.active && m.alien.setPipeline("Light2D")
    );
    Object.values(this.decorationElements).forEach((m) =>
      m.alien?.setPipeline("Light2D")
    );

    this.scene.events.on("player_moves", (nextPos) => {
      this.updateLight(nextPos);
      if(this.unfinished) {
        this.detectDocTeleporting(nextPos);
      }
    });
    // this.onNextTick(() => {
    //   this.teleportObject(this.leader, {
    //     x: this.exit.position.x - 3,
    //     y: this.exit.position.y,
    //   });
    // });
  }

  detectDocTeleporting(nextPos) {
    if (nextPos.y > 45 && this.leader.position.y < 10) {
      this.teleportObject(this.leader, {
        x: this.exit.position.x - 3,
        y: this.exit.position.y,
      });
    }
  }

  updateLight(nextPos) {
    if (!this.exit) return false;
    if (
      nextPos.x === this.exit.position.x - 3 &&
      nextPos.y === this.exit.position.y
    ) {
      this.player.light?.setRadius(500);
      this.player.light?.setIntensity(1.3);

      // remove alpha tween
      if (this.eyeAnimation) {
        this.exitEye.alpha = 0;
        this.eyeAnimation.remove();
      }
    } else if (
      nextPos.x === this.exit.position.x - 2 &&
      nextPos.y === this.exit.position.y
    ) {
      // this.player.light?.setIntensity(1.3)
      this.player.light?.setRadius(250);
      this.player.light?.setIntensity(0.8);

      // alpha tween from 0 to 0.5
      if (this.eyeAnimation) {
        this.eyeAnimation.remove();
      }
      this.exitEye.alpha = 0.1;
      this.eyeAnimation = this.scene.tweens.add({
        targets: this.exitEye,
        alpha: 0.2,
        duration: 100,
        yoyo: true,
        repeat: -1,
      });
    } else if (
      nextPos.x === this.exit.position.x - 1 &&
      nextPos.y === this.exit.position.y
    ) {
      // this.player.light?.setIntensity(1.3)
      this.player.light?.setIntensity(0.3);
      this.player.light?.setRadius(125);

      // alpha tween from 0.5 to 1
      if (this.eyeAnimation) {
        this.eyeAnimation.remove();
      }
      this.exitEye.alpha = 0.3;
      this.eyeAnimation = this.scene.tweens.add({
        targets: this.exitEye,
        alpha: 0.5,
        duration: 100,
        yoyo: true,
        repeat: -1,
      });
    } else if (
      nextPos.x === this.exit.position.x &&
      nextPos.y === this.exit.position.y
    ) {
      this.player.light?.setIntensity(0.1);
    } else if (this.player.light?.intensity !== 1.5) {
      this.player.light?.setIntensity(1.5);
      this.player.light?.setRadius(1000);
    }
  }

  onInteractWith(el) {
    super.onInteractWith(el);

    if (el.isClass(Exit)) {
      this.onComplete();
    }

    if (el.isClass(Interactive) && el.order === 0 && this.unfinished) {
      this.showDialog1();
    }

    if (el.isClass(Interactive) && el.order === 1 && this.unfinished) {
      if (this.collectedDiamonds < this.diamondsGoal) {
        this.showDialogGoal();
      } else {
        this.moveToExit()
        this.interactiveElements[`${el.position.x}-${el.position.y}`] = null
      }
    }
  }

  onAddElement(el) {
    if (el.isClass(Player)) {
      el.power = 1;
      el.hasPickaxe = true;
      // just a hack for presentation before torch is lit
      el.alien.alpha = 0.5;

      this.onNextTick(lvl => {
        if(!lvl.unfinished) {
          el.ligthTorch();
        }
      })
    }

    if (el.isClass(Exit)) {
      const layer = this.scene.children.getByName("layer_2");
      const map = layer.tilemap;

      this.exitEye = this.scene.add.sprite(48, 16, "spark").setOrigin(0.5, 0.5);
      this.exitEye.setTintFill(0xd25400);
      this.exitEye.setScale(layer.scaleX * 2, layer.scaleX * 4);
      this.exitEye.x = map.tileToWorldX(el.position.x - 0.5);
      this.exitEye.y = map.tileToWorldY(el.position.y - 0.4);
      this.exitEye.depth = 100;
      this.exitEye.alpha = 0;
    }

    if (el.isClass(Torch) && el.order === 0) {
      el.ignite();
    }

    if (el.isClass(Interactive) && el.order === 1) {
      el.ephemeral = false;
    }
  }

  showDialog1() {
    this.scene.events.once("dialogue:hidden", () => {
      this.player.ligthTorch();
      this.setTickTimeout(() => {
        this.showDialog2();
      }, 10);
    });
    this.scene.events.emit("dialogue:show", {
      participants: ["closeUp_Dwarf", "closeUp_Mary"],
      participantNames: ["Doc", "Mary"],
      conversation: [
        {
          who: "closeUp_Mary",
          text: [translate("level_17.dialog_1.mary_say_1")],
        },
        {
          who: "closeUp_Dwarf",
          text: [translate("level_17.dialog_1.dwarf_say_1")],
        },
      ],
    });
  }
  showDialog2() {
    this.scene.events.emit("dialogue:show", {
      participants: ["closeUp_Dwarf", "closeUp_Mary"],
      participantNames: ["Doc", "Mary"],
      conversation: [
        {
          who: "closeUp_Mary",
          text: [translate("level_17.dialog_2.mary_say_1")],
        },
        {
          who: "closeUp_Dwarf",
          text: [translate("level_17.dialog_2.dwarf_say_1")],
        },
      ],
    });
  }
  showDialogGoal() {
    this.scene.events.emit("dialogue:show", {
      participants: ["closeUp_Mary", "closeUp_Dwarf"],
      participantNames: ["Mary", "Doc"],
      conversation: [
        {
          who: "closeUp_Dwarf",
          text: [
            template(translate("levels.goal_dialog.dwarf_say_1"))({
              GOAL: this.diamondsGoal,
              NUM: this.collectedDiamonds,
            }),
          ],
        },
      ],
    });
  }

  async moveToExit() {
    this.player.disconnect();
    this.dwarfRun = true;
    this.leader.leadingPath = [
      { x: this.leader.position.x + 1, y: this.leader.position.y },
      { x: this.leader.position.x + 1, y: this.leader.position.y },
      { x: this.leader.position.x + 2, y: this.leader.position.y },
      { x: this.leader.position.x + 2, y: this.leader.position.y },
      { x: this.leader.position.x + 3, y: this.leader.position.y },
      { x: this.leader.position.x + 3, y: this.leader.position.y },
    ];
    this.moveDwarf();

    // anim angel dismiss
    this.scene.tweens.add({
      targets: this.exit.block.alien,
      x: UI.tileToScreen(this.exit.position.x),
      duration: TICK_TIME * 2,
      onComplete: () => {
        super.destroyBlock()
        // auto pilot off
        this.player.reconnect();
      },
    });
  }
  moveDwarf() {
    // hack to auto move dwarf
    if (this.dwarfRun) {
      if (this.leader.leadingPath.length !== 0) {
        this.onNextTick(() => {
          this.leader.onPlayerMoves(this.leader.position);
          this.moveDwarf();
        });
      } else {
        this.dwarfRun = false;
        this.replaceByEmpty(this.leader);
        this.leader = null;
      }
    }
  }
}

Level17.key = "level_17";
export default Level17;
